<template>
    <div class="awesome-class">
        {{awesome}}
         <el-table :data="gridData.data" emptyText="" border style="width: 100%" >
         </el-table>
        <div class="form-group">
            <input type="text" class="form-control" placeholder="text"/>
        </div>
    </div>
</template>


<script>

export default {
    name: 'clientsGrid',
    data() {
        return {
            awesome: "text1",
            clients: [],
            gridData: {
                data: this.data,
                selected: [],
            },
        }
    },
    computed: {
    },
    created() {
        this.$http.get('api/client')
            .then(response => this.clients = response.data.clientList);
    },
    methods: {
    }
}
</script>

<style lang="sass">
.awesome-class
    font-size: 15px
    margin-top: 60px  
</style>



